  flex-direction: column;
  padding: 0.5vw !important;
  min-width: 6vw !important;
  border-radius: 0.3vw !important;
}
.gallery-nav-items p {
  font-size: 0.9vw;
}

.gallery-img .jss6 {
  border-radius: 0.8vw !important;
}
.gallery-img .jss109 {
  width: 25vw;
  position: relative;
  flex-basis: auto;
  min-height: 1px;
}
