  width: 90%;
  margin-bottom: 1vw;
}
.gallery-img .jss10 {
  height: 24vw;
}

.jss108 {
  width: -webkit-fill-available !important;
}

.image {
  width: 100%;
  height: auto;
}

.svg_icons {
  font-size: 2vw !important;
  margin-top: 2vw;
}

