  width: 100vw; /* Ensure navbar covers full width */
  z-index: 1000; /* Ensure navbar stays on top */
  background-color: transparent !important;
  box-shadow: none;
  transition: background-color 0.3s ease;
  padding: 0 !important;
}

.toolbar-trans {
  background-color: transparent !important;
  z-index: 2;
}

.toolbar-opq {
  background-color: #e9ae00 !important;
  transition: background-color 0.7s ease;
  z-index: 2;
}

.navbar-title {
  width: fit-content;
  margin-left: 4.8vw !important; /* Push logo to extreme left */
  cursor: pointer;
  font-family: "Monoton", sans-serif !important;
  user-select: none;
}

.navbar-title-center {
  width: 100% !important;
  justify-content: center !important;
  cursor: pointer;
  font-family: "Monoton", sans-serif !important;
}

.navbar-title-space {
  width: 78% !important;
  position: relative;
  left: -9vw;
}

.nav-items-colord {
  color: #e9ae00;
}

.nav-items-white {
  color: white;
}

.navbar-nav {
  text-align: center;
  flex-grow: 1; /* Make other nav elements align center */
  width: 70vw;
  max-width: 72vw;
  justify-content: space-evenly;
  cursor: pointer;
  font-family: "Monoton", sans-serif !important;
}

.nav-item {
  font-family: "Freeman", sans-serif !important;
  font-weight: 400;
  font-style: normal;
  user-select: none;
}

.nav-menu-item {
  margin: 1vw 2vw !important;
}

.nav-menu-item:hover {
  font-style: oblique;
  color: #e9ae00;
}

.nav-menu-item:click {
  font-style: oblique;
  color: #e9ae00;
}

.social-icons {
  margin-right: 1vw; /* Add social media icons to the extreme right */
  margin-top: 5px;
  justify-content: space-evenly !important;
  width: 14vw;
  cursor: pointer;
}

.social-icons a {
  margin-left: 1vw; /* Add spacing between social media icons */
