___CSS_LOADER_EXPORT___.push([module.id, `.preview-img-container {
  width: 100%;
  height: 50vw;
  overflow: hidden;
  position: relative;
  top: 0vw;
  display: flex;
  justify-content: center;
  align-items: center;
}
.preview-img-container .preview-img {
  width: 100%;
  height: 50vw;
  position: absolute;
  top: 0vw;
  left: 0;
  object-fit: cover;
  z-index: -1;
}
.preview-img-container .overlay {
  position: absolute;
  top: 0vw;
  left: 0vw;
  width: 100%;
  height: 50vw;
  background-color: rgba(0, 0, 0, 0.5);
  z-index: 1;
}
.preview-img-container .overlay .overlay-content {
  position: absolute;
  top: 30%;
  left: 5%;
  width: 90%;
  height: 2vw;
  user-select: none;
}
.preview-img-container .overlay .overlay-content .overlay-text {
  position: unset;
  color: white;
  font-size: 5vw;
  font-weight: bold;
  text-align: center;
  font-family: "LXGW WenKai TC", cursive;
  font-style: normal;
}
.preview-img-container .overlay .overlay-content .overlay-text-small {
  position: relative;
  top: -1vw;
  color: white;
  font-size: 0.9vw;
  font-weight: bold;
  text-align: center;
  font-family: "LXGW WenKai TC", cursive;
  font-style: normal;
